import React from 'react';
import { Box, Container, Typography, Button } from '@mui/material';
import { Link } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const GradientText = styled(Typography)(({ theme }) => ({
  background: 'linear-gradient(135deg, #FF0000 0%, #FF4444 100%)',
  WebkitBackgroundClip: 'text',
  WebkitTextFillColor: 'transparent',
  textAlign: 'center',
  fontWeight: 800,
  textTransform: 'uppercase',
  letterSpacing: '2px',
  marginBottom: '20px',
  fontSize: '3rem',
  [theme.breakpoints.down('sm')]: {
    fontSize: '2rem',
  },
}));

const IconWrapper = styled(Box)(({ theme }) => ({
  display: 'flex',
  justifyContent: 'center',
  marginBottom: '2rem',
  '& svg': {
    fontSize: '5rem',
    color: '#FF0000',
    animation: 'pulse 2s infinite',
  },
  '@keyframes pulse': {
    '0%': {
      transform: 'scale(1)',
      opacity: 1,
    },
    '50%': {
      transform: 'scale(1.1)',
      opacity: 0.7,
    },
    '100%': {
      transform: 'scale(1)',
      opacity: 1,
    },
  },
}));

const ThankYou = () => {
  return (
    <Box
      sx={{
        background: 'linear-gradient(180deg, rgba(26, 26, 26, 0.98) 0%, rgba(20, 20, 20, 0.99) 100%)',
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        py: 8,
      }}
    >
      <Container maxWidth="md">
        <Box
          sx={{
            textAlign: 'center',
            p: 4,
            borderRadius: 4,
            background: 'rgba(0, 0, 0, 0.3)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.1)',
          }}
        >
          <IconWrapper>
            <CheckCircleIcon />
          </IconWrapper>
          
          <GradientText variant="h1">
            Merci pour votre message !
          </GradientText>
          
          <Typography
            variant="h5"
            sx={{
              color: 'rgba(255, 255, 255, 0.9)',
              mb: 4,
              fontWeight: 400,
              lineHeight: 1.6,
            }}
          >
            Nous avons bien reu votre demande et nous vous rpondrons dans les plus brefs dlais.
          </Typography>

          <Button
            component={Link}
            to="/"
            variant="contained"
            sx={{
              background: 'linear-gradient(45deg, #FF0000 30%, #FF4444 90%)',
              color: 'white',
              padding: '12px 30px',
              fontSize: '1.1rem',
              fontWeight: 600,
              borderRadius: '50px',
              textTransform: 'none',
              boxShadow: '0 4px 15px rgba(255, 0, 0, 0.2)',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              '&:hover': {
                background: 'linear-gradient(45deg, #FF4444 30%, #FF0000 90%)',
                transform: 'translateY(-2px)',
                boxShadow: '0 6px 20px rgba(255, 0, 0, 0.3)',
              },
            }}
          >
            Retour  l'accueil
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default ThankYou; 