import React from 'react';
import {
  Container,
  Typography,
  Grid,
  Box,
  Paper,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import PeopleIcon from '@mui/icons-material/People';
import BuildIcon from '@mui/icons-material/Build';
import SecurityIcon from '@mui/icons-material/Security';

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  textAlign: 'center',
  background: 'linear-gradient(145deg, rgba(25, 25, 25, 0.95), rgba(15, 15, 15, 0.97))',
  backdropFilter: 'blur(10px)',
  borderRadius: '20px',
  border: '1px solid rgba(255, 255, 255, 0.05)',
  transition: 'all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
  '&:hover': {
    transform: 'translateY(-10px)',
    boxShadow: '0 20px 40px rgba(0, 0, 0, 0.4)',
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    height: '2px',
    background: 'linear-gradient(90deg, transparent, rgba(255, 0, 0, 0.3), transparent)',
  },
}));

const IconCircle = styled(Box)(({ theme }) => ({
  width: '80px',
  height: '80px',
  borderRadius: '50%',
  background: 'rgba(139, 0, 0, 0.4)',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  marginBottom: '25px',
  transition: 'all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
  '& svg': {
    fontSize: '35px',
    color: '#FFFFFF',
    transition: 'all 0.3s ease',
  },
  '&:hover': {
    transform: 'scale(1.1) rotate(5deg)',
    background: 'linear-gradient(135deg, rgba(139, 0, 0, 0.8), rgba(255, 0, 0, 0.4))',
    boxShadow: '0 0 20px rgba(255, 0, 0, 0.3)',
  },
}));

const GradientText = styled(Typography)(({ theme }) => ({
  background: 'linear-gradient(135deg,rgb(252, 242, 242) 0%,rgb(238, 234, 234) 100%)',
  WebkitBackgroundClip: 'text',
  WebkitTextFillColor: 'transparent',
  textAlign: 'center',
  fontWeight: 800,
  textTransform: 'uppercase',
  letterSpacing: '2px',
  marginBottom: '10px',
  fontSize: '3.5rem',
  lineHeight: 1.2,
  textShadow: '0 0 30px rgb(252, 246, 246)',
  '@media (max-width: 600px)': {
    fontSize: '2.5rem',
  }
}));

const StyledText = styled(Typography)(({ theme }) => ({
  color: 'rgba(255, 255, 255, 0.95)',
  textAlign: 'center',
  lineHeight: 1.6,
  fontSize: '1.8rem',
  fontWeight: 500,
  letterSpacing: '0.5px',
  '@media (max-width: 600px)': {
    fontSize: '1.4rem',
  }
}));

const About = () => {
  const stats = [
    { number: '10+', label: 'Annes d\'Exprience', icon: <EmojiEventsIcon /> },
    { number: '50+', label: 'Techniciens Experts', icon: <PeopleIcon /> },
    { number: '1000+', label: 'Vhicules Entretenus', icon: <BuildIcon /> },
    { number: '99%', label: 'Satisfaction Client', icon: <SecurityIcon /> },
  ];

  return (
    <Box sx={{
      background: 'linear-gradient(180deg, rgba(26, 26, 26, 0.98) 0%, rgba(20, 20, 20, 0.99) 100%)',
      minHeight: '100vh',
      py: 8,
    }}>
      <Container maxWidth="lg">
        <Box sx={{ textAlign: 'center', mb: 8 }}>
          <GradientText variant="h1">
             Propos de Nous
          </GradientText>
          <StyledText sx={{ maxWidth: '800px', mx: 'auto', mb: 6 }}>
            Notre quipe de professionnels qualifis s'engage  fournir des services de vitrage automobile d'excellence, avec une expertise reconnue et un service client exceptionnel.
          </StyledText>
        </Box>

        <Grid container spacing={4} sx={{ mb: 8 }}>
          {stats.map((stat, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <StyledPaper>
                <IconCircle>{stat.icon}</IconCircle>
                <Typography variant="h3" sx={{ 
                  color: '#FFFFFF',
                  fontSize: '2.5rem',
                  fontWeight: 700,
                  mb: 2 
                }}>
                  {stat.number}
                </Typography>
                <Typography variant="h6" sx={{ 
                  color: 'rgba(255, 255, 255, 0.8)',
                  fontSize: '1.1rem'
                }}>
                  {stat.label}
                </Typography>
              </StyledPaper>
            </Grid>
          ))}
        </Grid>

        <Box sx={{ mb: 8 }}>
          <Typography variant="h4" sx={{
            textAlign: 'center',
            mb: 4,
            color: '#FFFFFF',
            fontSize: '2.2rem',
            fontWeight: 700,
            background: 'linear-gradient(135deg, #FF0000 0%, #FF5555 100%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
          }}>
            Notre Histoire
          </Typography>
          <StyledPaper sx={{ p: 4 }}>
            <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.9)', mb: 3, fontSize: '1.1rem', lineHeight: 1.8 }}>
              Fonde en 2010, Mario Glass est passe d'une petite entreprise locale  un leader dans les services de vitrage automobile. Notre engagement envers la qualit et la satisfaction client nous a valu une rputation d'excellence dans l'industrie.
            </Typography>
            <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.9)', fontSize: '1.1rem', lineHeight: 1.8 }}>
              Nous croyons en l'utilisation exclusive de matriaux de la plus haute qualit et des technologies les plus rcentes pour garantir les meilleurs rsultats  nos clients. Notre quipe de techniciens certifis suit rgulirement des formations pour rester  jour avec les dernires normes et techniques de l'industrie.
            </Typography>
          </StyledPaper>
        </Box>

        <Box>
          <Typography variant="h4" sx={{
            textAlign: 'center',
            mb: 4,
            color: '#FFFFFF',
            fontSize: '2.2rem',
            fontWeight: 700,
            background: 'linear-gradient(135deg, #FF0000 0%, #FF5555 100%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
          }}>
            Nos Valeurs
          </Typography>
          <Grid container spacing={4}>
            <Grid item xs={12} md={4}>
              <StyledPaper>
                <IconCircle>
                  <SecurityIcon />
                </IconCircle>
                <Typography variant="h6" sx={{ color: '#FFFFFF', mb: 2, fontSize: '1.4rem' }}>
                  Qualit
                </Typography>
                <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.9)', fontSize: '1.1rem', lineHeight: 1.6 }}>
                  Nous ne faisons jamais de compromis sur la qualit. Chaque service que nous fournissons rpond aux normes les plus leves de l'industrie.
                </Typography>
              </StyledPaper>
            </Grid>
            <Grid item xs={12} md={4}>
              <StyledPaper>
                <IconCircle>
                  <BuildIcon />
                </IconCircle>
                <Typography variant="h6" sx={{ color: '#FFFFFF', mb: 2, fontSize: '1.4rem' }}>
                  Intgrit
                </Typography>
                <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.9)', fontSize: '1.1rem', lineHeight: 1.6 }}>
                  Nous croyons en un service honnte et transparent, avec des prix quitables pour tous nos clients.
                </Typography>
              </StyledPaper>
            </Grid>
            <Grid item xs={12} md={4}>
              <StyledPaper>
                <IconCircle>
                  <PeopleIcon />
                </IconCircle>
                <Typography variant="h6" sx={{ color: '#FFFFFF', mb: 2, fontSize: '1.4rem' }}>
                  Service Client
                </Typography>
                <Typography variant="body1" sx={{ color: 'rgba(255, 255, 255, 0.9)', fontSize: '1.1rem', lineHeight: 1.6 }}>
                  Votre satisfaction est notre priorit absolue. Nous allons au-del pour garantir une exprience positive.
                </Typography>
              </StyledPaper>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </Box>
  );
};

export default About; 