import React, { useState } from 'react';
import {
  Container,
  Typography,
  TextField,
  Button,
  Grid,
  Paper,
  Box,
  Snackbar,
  Alert,
  useTheme,
  IconButton,
  InputAdornment,
  CircularProgress,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import SendIcon from '@mui/icons-material/Send';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PersonIcon from '@mui/icons-material/Person';
import MessageIcon from '@mui/icons-material/Message';
import SubjectIcon from '@mui/icons-material/Subject';
import BusinessIcon from '@mui/icons-material/Business';
import WorkIcon from '@mui/icons-material/Work';
import EmailCard from '../components/EmailCard';

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  textAlign: 'center',
  background: 'linear-gradient(145deg, rgba(25, 25, 25, 0.95), rgba(15, 15, 15, 0.97))',
  backdropFilter: 'blur(10px)',
  borderRadius: '20px',
  border: '1px solid rgba(255, 255, 255, 0.05)',
  transition: 'all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
  '&:hover': {
    transform: 'translateY(-10px)',
    boxShadow: '0 20px 40px rgba(0, 0, 0, 0.4)',
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    height: '2px',
    background: 'linear-gradient(90deg, transparent, rgba(255, 0, 0, 0.3), transparent)',
  },
}));

const IconCircle = styled(Box)(({ theme }) => ({
  width: '80px',
  height: '80px',
  borderRadius: '50%',
  background: 'rgba(139, 0, 0, 0.4)',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  marginBottom: '25px',
  transition: 'all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
  '& svg': {
    fontSize: '35px',
    color: '#FFFFFF',
    transition: 'all 0.3s ease',
  },
  '&:hover': {
    transform: 'scale(1.1) rotate(5deg)',
    background: 'linear-gradient(135deg, rgba(139, 0, 0, 0.8), rgba(255, 0, 0, 0.4))',
    boxShadow: '0 0 20px rgba(255, 0, 0, 0.3)',
  },
}));

const GradientText = styled(Typography)(({ theme }) => ({
  background: 'linear-gradient(135deg,rgb(250, 245, 245) 0%,rgb(245, 238, 238) 100%)',
  WebkitBackgroundClip: 'text',
  WebkitTextFillColor: 'transparent',
  textAlign: 'center',
  fontWeight: 800,
  textTransform: 'uppercase',
  letterSpacing: '2px',
  marginBottom: '10px',
  fontSize: '3.5rem',
  lineHeight: 1.2,
  textShadow: '0 0 30px rgb(252, 246, 246)',
  '@media (max-width: 600px)': {
    fontSize: '2.5rem',
  }
}));

const StyledText = styled(Typography)(({ theme }) => ({
  color: 'rgba(255, 255, 255, 0.95)',
  textAlign: 'center',
  lineHeight: 1.6,
  fontSize: '1.8rem',
  fontWeight: 500,
  letterSpacing: '0.5px',
  '@media (max-width: 600px)': {
    fontSize: '1.4rem',
  }
}));

const StyledTextField = styled(TextField)(({ theme }) => ({
  '& .MuiOutlinedInput-root': {
    color: 'rgba(255, 255, 255, 0.9)',
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    borderRadius: '12px',
    '& fieldset': {
      borderColor: 'rgba(255, 255, 255, 0.2)',
    },
    '&:hover fieldset': {
      borderColor: 'rgba(255, 0, 0, 0.5)',
    },
    '&.Mui-focused fieldset': {
      borderColor: '#FF0000',
    },
  },
  '& .MuiInputLabel-root': {
    color: 'rgba(255, 255, 255, 0.7)',
  },
  '& .MuiInputAdornment-root': {
    color: 'rgba(255, 255, 255, 0.7)',
    marginRight: '12px',
    '& .MuiSvgIcon-root': {
      fontSize: '20px',
      transition: 'all 0.3s ease',
    },
    '&:hover .MuiSvgIcon-root': {
      color: '#FF0000',
      transform: 'scale(1.1)',
    }
  },
}));

const Contact = () => {
  const theme = useTheme();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    message: '',
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success',
  });

  const validateForm = () => {
    const newErrors = {};
    if (!formData.name.trim()) {
      newErrors.name = 'Le nom est requis';
    }
    if (!formData.email.trim()) {
      newErrors.email = 'L\'email est requis';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Email invalide';
    }
    if (!formData.message.trim()) {
      newErrors.message = 'Le message est requis';
    }
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value,
    }));
    // Clear error when user types
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: '',
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) return;

    setLoading(true);
    const form = e.target;
    const formData = new FormData(form);

    try {
      const response = await fetch(form.action, {
        method: 'POST',
        body: formData,
        headers: {
          'Accept': 'application/json'
        }
      });

      if (response.ok) {
        setSnackbar({
          open: true,
          message: 'Message envoy avec succs ! Nous vous rpondrons bientt.',
          severity: 'success',
        });
        setFormData({
          name: '',
          email: '',
          phone: '',
          message: '',
        });
      } else {
        throw new Error('Erreur lors de l\'envoi du message');
      }
    } catch (error) {
      setSnackbar({
        open: true,
        message: 'chec de l\'envoi du message. Veuillez ressayer plus tard.',
        severity: 'error',
      });
    } finally {
      setLoading(false);
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({ ...prev, open: false }));
  };

  const contactInfo = [
    {
      icon: <LocationOnIcon />,
      title: 'Adresse',
      content: 'Toute la France'
    },
    {
      icon: <PhoneIcon />,
      title: 'Tlphone',
      content: '+33 7 45 81 87 32'
    },
    {
      icon: <EmailIcon />,
      title: 'Email',
      content: 'Contact@marioglass.fr'
    },
    {
      icon: <AccessTimeIcon />,
      title: 'Horaires',
      content: '7/7j : 9h - 21h'
    }
  ];

  return (
    <Box sx={{
      background: 'linear-gradient(180deg, rgba(26, 26, 26, 0.98) 0%, rgba(20, 20, 20, 0.99) 100%)',
      minHeight: '100vh',
      py: 8,
    }}>
      <Container maxWidth="lg">
        <Box sx={{ textAlign: 'center', mb: 8 }}>
          <GradientText variant="h1">
            Contactez-nous
          </GradientText>
          <StyledText sx={{ maxWidth: '800px', mx: 'auto', mb: 6 }}>
            Prenez contact avec nous pour toute question ou pour planifier un rendez-vous.
          </StyledText>
        </Box>

        <Grid container spacing={4}>
          <Grid item xs={12} md={6}>
            <StyledPaper sx={{ p: 4 }}>
              <Typography variant="h5" sx={{ 
                color: '#FFFFFF',
                fontSize: '1.8rem',
                fontWeight: 600,
                mb: 4,
                background: 'linear-gradient(135deg, #FF0000 0%, #FF5555 100%)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
              }}>
                Envoyez-nous un Message
              </Typography>
              <form 
                action="https://formspree.io/f/mqaqzdly" 
                method="POST"
                style={{ width: '100%' }}
              >
                <input type="hidden" name="_subject" value="Nouveau message du formulaire de contact" />
                <input type="hidden" name="_next" value="https://marioglass.fr/thank-you" />
                <input type="text" name="_gotcha" style={{ display: 'none' }} />
                
                <Grid container spacing={3}>
                  <Grid item xs={12}>
                    <StyledTextField
                      fullWidth
                      label="Nom"
                      name="name"
                      required
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <PersonIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <StyledTextField
                      fullWidth
                      label="Entreprise"
                      name="company"
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <BusinessIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <StyledTextField
                      fullWidth
                      label="Profession"
                      name="profession"
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <WorkIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <StyledTextField
                      fullWidth
                      label="Email"
                      name="email"
                      type="email"
                      required
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <EmailIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <StyledTextField
                      fullWidth
                      label="Tlphone"
                      name="phone"
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <PhoneIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <StyledTextField
                      fullWidth
                      label="Sujet"
                      name="subject"
                      required
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <SubjectIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <StyledTextField
                      fullWidth
                      label="Message"
                      name="message"
                      multiline
                      rows={4}
                      required
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start" sx={{ alignSelf: 'flex-start', mt: 1.5 }}>
                            <MessageIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Button
                      type="submit"
                      variant="contained"
                      size="large"
                      fullWidth
                      endIcon={<SendIcon />}
                      sx={{
                        background: 'linear-gradient(135deg, #FF0000 0%, #FF4444 100%)',
                        padding: '12px 30px',
                        fontSize: '1.1rem',
                        fontWeight: 600,
                        borderRadius: '50px',
                        textTransform: 'none',
                        boxShadow: '0 4px 15px rgba(255, 0, 0, 0.2)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                        '&:hover': {
                          background: 'linear-gradient(135deg, #FF1111 0%, #FF5555 100%)',
                          transform: 'translateY(-2px)',
                          boxShadow: '0 6px 20px rgba(255, 0, 0, 0.3)',
                        }
                      }}
                    >
                      Envoyer le Message
                    </Button>
                  </Grid>
                </Grid>
              </form>
            </StyledPaper>
          </Grid>

          <Grid item xs={12} md={6}>
            <Grid container spacing={3}>
              {contactInfo.map((info, index) => (
                <Grid item xs={12} sm={6} key={index}>
                  <StyledPaper>
                    <IconCircle>
                      {info.icon}
                    </IconCircle>
                    <Typography variant="h6" sx={{ 
                      color: '#FFFFFF',
                      fontSize: '1.4rem',
                      fontWeight: 600,
                      mb: 2
                    }}>
                      {info.title}
                    </Typography>
                    <Typography variant="body1" sx={{ 
                      color: 'rgba(255, 255, 255, 0.9)',
                      fontSize: '1.1rem',
                      whiteSpace: 'pre-line'
                    }}>
                      {info.content}
                    </Typography>
                  </StyledPaper>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>

        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <EmailCard showIcon={true} />
        </Box>
      </Container>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Contact; 