import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link as RouterLink } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';

const StyledAppBar = styled(AppBar)(({ theme }) => ({
  background: 'linear-gradient(45deg, #1a1a1a 30%, #2a2a2a 90%)',
  boxShadow: '0 3px 5px 2px rgba(0, 0, 0, 0.3)',
  position: 'fixed',
  top: 0,
  left: 0,
  right: 0,
  zIndex: 1100,
  [theme.breakpoints.down('md')]: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
  }
}));

const Logo = styled('img')(({ theme }) => ({
  height: '40px',
  marginRight: theme.spacing(2),
  [theme.breakpoints.down('md')]: {
    height: '50px',
    marginRight: 0,
    transition: 'transform 0.3s ease',
    '&:hover': {
      transform: 'scale(1.05)',
    }
  },
  [theme.breakpoints.down('sm')]: {
    height: '45px',
  },
}));

const NavButton = styled(Button)(({ theme }) => ({
  color: '#ffffff',
  margin: '0 8px',
  '&:hover': {
    background: 'rgba(255, 255, 255, 0.1)',
  },
  [theme.breakpoints.down('md')]: {
    margin: '0 4px',
    padding: '6px 12px',
    fontSize: '0.9rem',
  },
}));

const StyledIconButton = styled(IconButton)(({ theme }) => ({
  marginRight: theme.spacing(2),
  color: '#fff',
  transition: 'all 0.3s ease',
  '&:hover': {
    transform: 'scale(1.1)',
    background: 'rgba(255, 255, 255, 0.1)',
  },
}));

const StyledDrawer = styled(Drawer)(({ theme }) => ({
  '& .MuiDrawer-paper': {
    width: 300,
    background: 'linear-gradient(145deg, rgba(12, 12, 12, 0.97), rgba(18, 18, 18, 0.98))',
    backdropFilter: 'blur(20px)',
    boxSizing: 'border-box',
    borderRight: '1px solid rgba(255, 255, 255, 0.05)',
    boxShadow: '5px 0 25px rgba(0, 0, 0, 0.5)',
    '&::before': {
      content: '""',
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      height: '2px',
      background: 'linear-gradient(90deg, transparent, rgba(255, 0, 0, 0.3), transparent)',
    },
  }
}));

const DrawerContent = styled(Box)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  paddingTop: theme.spacing(4),
  '& .logo-container': {
    padding: theme.spacing(0, 3, 5, 3),
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'relative',
    '&::after': {
      content: '""',
      position: 'absolute',
      bottom: 0,
      left: '50%',
      transform: 'translateX(-50%)',
      width: '40%',
      height: '1px',
      background: 'linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent)',
    },
    '& img': {
      height: '45px',
      transition: 'transform 0.3s ease',
      '&:hover': {
        transform: 'scale(1.05)',
      },
    },
  },
  '& .nav-section': {
    marginTop: theme.spacing(4),
  },
}));

const DrawerItem = styled(ListItem)(({ theme }) => ({
  margin: theme.spacing(0.8, 3),
  borderRadius: theme.spacing(1),
  transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
  position: 'relative',
  overflow: 'hidden',
  padding: theme.spacing(1.8, 2.5),
  '&:hover': {
    backgroundColor: 'rgba(255, 255, 255, 0.05)',
    transform: 'translateX(5px)',
  },
  '& .MuiListItemText-primary': {
    fontSize: '0.95rem',
    fontWeight: 500,
    color: 'rgba(255, 255, 255, 0.85)',
    transition: 'all 0.3s ease',
    letterSpacing: '0.6px',
    textTransform: 'uppercase',
  },
  '&:hover .MuiListItemText-primary': {
    color: '#ffffff',
  },
}));

const ContactButton = styled(Button)(({ theme }) => ({
  width: '100%',
  padding: theme.spacing(1.5),
  color: '#ffffff',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-start',
  gap: theme.spacing(2),
  margin: theme.spacing(1, 0),
  borderRadius: theme.spacing(1),
  backgroundColor: 'rgba(255, 255, 255, 0.05)',
  transition: 'all 0.3s ease',
  textDecoration: 'none',
  '&:hover': {
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    transform: 'translateX(5px)',
  },
  '& .MuiSvgIcon-root': {
    fontSize: '1.5rem',
  },
}));

const ContactSection = styled(Box)(({ theme }) => ({
  marginTop: 'auto',
  padding: theme.spacing(3),
  borderTop: '1px solid rgba(255, 255, 255, 0.1)',
  '& > *:not(:last-child)': {
    marginBottom: theme.spacing(2),
  },
}));

const Navbar = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleNavClick = (event) => {
    if (isMobile) {
      handleDrawerToggle();
    }
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  const drawer = (
    <DrawerContent>
      <Box className="logo-container">
        <RouterLink to="/" style={{ textDecoration: 'none' }} onClick={handleNavClick}>
          <img src="/images/mario-glass-logo.png" alt="Mario Glass Logo" />
        </RouterLink>
      </Box>
      <Box className="nav-section">
        <List>
          <DrawerItem component={RouterLink} to="/" onClick={handleNavClick}>
            <ListItemText primary="Accueil" />
          </DrawerItem>
          <DrawerItem component={RouterLink} to="/about" onClick={handleNavClick}>
            <ListItemText primary=" Propos" />
          </DrawerItem>
          <DrawerItem component={RouterLink} to="/contact" onClick={handleNavClick}>
            <ListItemText primary="Contact" />
          </DrawerItem>
        </List>
      </Box>
      <ContactSection>
        <Typography variant="subtitle2" sx={{ color: 'rgba(255, 255, 255, 0.7)', mb: 2, textAlign: 'center' }}>
          Contactez-nous
        </Typography>
        <ContactButton
          component="a"
          href="tel:+32474063817"
          startIcon={<PhoneIcon />}
          onClick={handleDrawerToggle}
        >
          +32 474 06 38 17
        </ContactButton>
        <ContactButton
          component="a"
          href="mailto:Contact@marioglass.fr"
          startIcon={<EmailIcon />}
          onClick={handleDrawerToggle}
        >
          Contact@marioglass.fr
        </ContactButton>
      </ContactSection>
    </DrawerContent>
  );

  return (
    <StyledAppBar position="fixed">
      <Toolbar sx={{ position: 'relative' }}>
        {isMobile && (
          <StyledIconButton
            edge="start"
            onClick={handleDrawerToggle}
            aria-label="menu"
            sx={{
              position: 'absolute',
              left: 16,
              zIndex: 2
            }}
          >
            <MenuIcon />
          </StyledIconButton>
        )}
        
        {isMobile ? (
          <Box sx={{ 
            position: 'absolute',
            left: 0,
            right: 0,
            display: 'flex',
            justifyContent: 'center',
            zIndex: 1
          }}>
            <Box component="div" sx={{ textDecoration: 'none' }}>
              <Logo src="/images/mario-glass-logo.png" alt="Mario Glass Logo" />
            </Box>
          </Box>
        ) : (
          <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
            <RouterLink to="/" style={{ textDecoration: 'none', marginRight: 'auto' }} onClick={handleNavClick}>
              <Logo src="/images/mario-glass-logo.png" alt="Mario Glass Logo" />
            </RouterLink>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <NavButton component={RouterLink} to="/" onClick={handleNavClick}>
                Accueil
              </NavButton>
              <NavButton component={RouterLink} to="/about" onClick={handleNavClick}>
                 Propos
              </NavButton>
              <NavButton component={RouterLink} to="/contact" onClick={handleNavClick}>
                Contact
              </NavButton>
            </Box>
          </Box>
        )}
      </Toolbar>
      {isMobile && (
        <StyledDrawer
          anchor="left"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
        >
          {drawer}
        </StyledDrawer>
      )}
    </StyledAppBar>
  );
};

export default Navbar; 