import React from 'react';
import { Box, Typography, IconButton, useTheme } from '@mui/material';
import { styled } from '@mui/material/styles';
import EmailIcon from '@mui/icons-material/Email';

const StyledEmailContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  gap: theme.spacing(2),
  padding: theme.spacing(1.5),
  borderRadius: theme.spacing(1),
  transition: 'all 0.3s ease',
  cursor: 'pointer',
  '&:hover': {
    background: 'rgba(255, 255, 255, 0.05)',
    transform: 'translateY(-2px)',
  },
}));

const StyledIconButton = styled(IconButton)(({ theme }) => ({
  background: 'linear-gradient(45deg, #8B0000 30%, #FF0000 90%)',
  color: theme.palette.common.white,
  padding: theme.spacing(1.2),
  '&:hover': {
    background: 'linear-gradient(45deg, #FF0000 30%, #8B0000 90%)',
    transform: 'scale(1.1)',
  },
  transition: 'all 0.3s ease',
}));

const EmailCard = ({ email = 'Contact@marioglass.fr', onClick, showIcon = true }) => {
  const theme = useTheme();

  const handleClick = (e) => {
    e.preventDefault();
    if (onClick) {
      onClick();
    }
    window.location.href = `mailto:${email}`;
  };

  return (
    <StyledEmailContainer onClick={handleClick}>
      {showIcon && (
        <StyledIconButton size="small">
          <EmailIcon />
        </StyledIconButton>
      )}
      <Typography
        variant="body1"
        sx={{
          color: theme.palette.common.white,
          fontWeight: 500,
          letterSpacing: '0.5px',
          '@media (max-width: 600px)': {
            fontSize: '0.9rem',
          }
        }}
      >
        {email}
      </Typography>
    </StyledEmailContainer>
  );
};

export default EmailCard; 